.bv-page {
  padding: 3.25rem 2rem 2rem; }
  .bv-page__header {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0 0 1rem;
    display: flex;
    flex-wrap: wrap; }
    .bv-page__header-icon {
      font-size: 1.5rem;
      height: 2.25rem;
      padding: 0.375rem 0;
      vertical-align: top;
      margin-right: 0.5rem;
      color: rgba(0, 0, 0, 0.65);
      flex: none; }
    .bv-page__header-content {
      flex: 1; }
    .bv-page__header-actions {
      flex: none;
      text-align: center;
      margin-top: 0.5rem;
      width: 100%; }
  .bv-page__breadcrumbs {
    width: 100%;
    flex: none; }
  @media only screen and (min-width: 600px) {
    .bv-page__header-actions {
      margin-top: 0;
      margin-left: 0.5rem;
      text-align: left;
      width: auto; } }
  .bv-page__title {
    margin: 0;
    line-height: 2.25rem; }
  .bv-page__description {
    color: rgba(0, 0, 0, 0.65); }
  .bv-page__segment {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 2rem;
    padding-bottom: 2rem;
    display: flex;
    flex-wrap: wrap; }
    .bv-page__segment:last-child {
      border-bottom: none; }
  .bv-page__segment-content {
    flex: 1 1 25rem;
    width: 0px; }
  .bv-page__annotation {
    flex: 0 1 15rem;
    margin-right: 1rem;
    margin-bottom: 1rem; }
  .bv-page__annotation-details {
    color: rgba(0, 0, 0, 0.65); }
  @media only screen and (min-width: 900px) {
    .bv-page {
      padding-top: 2rem; } }
  .bv-page__footer {
    width: calc(100% + 4rem);
    margin-left: -2rem;
    padding-left: 2rem;
    padding-right: 2rem;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    display: flex;
    flex-direction: column;
    transition: height 3s ease;
    background-color: #F6F7F8;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 6; }
    .bv-page__footer-bar {
      padding: 1.5rem 0 1.5rem 0.875rem;
      display: flex;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap; }
      .bv-page__footer-bar-spacer {
        flex-grow: 1; }
    .bv-page__footer-actions {
      flex: none;
      text-align: center;
      margin-top: 0.5rem;
      flex-shrink: 0; }
