.bv-paginator {
  display: inline-flex;
  align-items: center; }
  .bv-paginator__input {
    max-width: 7rem; }
    .bv-paginator__input > .bv-input__input-element {
      width: 2.5rem;
      text-align: right; }
      .bv-paginator__input > .bv-input__input-element[type='number'] {
        -moz-appearance: textfield; }
      .bv-paginator__input > .bv-input__input-element::-webkit-inner-spin-button, .bv-paginator__input > .bv-input__input-element::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0; }
    .bv-paginator__input > .bv-input__suffix {
      width: 3rem;
      text-align: left;
      margin-left: 0.25rem; }
