.bv-popover {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: transparent;
  z-index: 500;
  opacity: 0;
  visibility: hidden;
  transition: 0.15s ease; }
  .bv-popover__target-reference {
    position: absolute;
    z-index: 500; }
  .bv-popover__placement {
    max-width: 20rem;
    min-width: 10rem;
    width: 100vw;
    min-height: 2.5rem;
    max-height: 50vh;
    position: absolute;
    padding: 0.25rem; }
  .bv-popover__card {
    background: #fff;
    border-radius: 0.5rem;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.15);
    transition: 0.15s ease; }
  .bv-popover__content {
    height: 100%;
    overflow-y: auto;
    padding: 1rem; }
  .bv-popover__section {
    padding: 1rem;
    margin: 0 -1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
    .bv-popover__section:first-child {
      padding-top: 1rem;
      margin-top: -1rem; }
    .bv-popover__section:last-child {
      padding-bottom: 1rem;
      margin-bottom: -1rem;
      border-bottom: none; }
    .bv-popover__section-header {
      display: flex;
      margin-bottom: 0.25rem;
      align-items: baseline; }
      .bv-popover__section-header-title {
        flex: 1;
        font-size: 0.8125rem;
        text-transform: uppercase;
        color: rgba(0, 0, 0, 0.65);
        font-weight: 500;
        letter-spacing: 0.25px;
        margin-bottom: 0; }
      .bv-popover__section-header-accessory {
        flex: none;
        margin-left: 0.25rem;
        font-size: 0.8125rem; }
  .bv-popover[class*=--top] {
    -webkit-transform: translateY(1rem);
            transform: translateY(1rem); }
  .bv-popover[class*=--left] {
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem); }
  .bv-popover[class*=--right] {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem); }
  .bv-popover[class*=--bottom] {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem); }
  .bv-popover--top-start .bv-popover__card {
    margin-left: -0.25rem; }
  .bv-popover--top-end .bv-popover__card {
    margin-right: -0.25rem; }
  .bv-popover--left-start {
    margin-top: -0.25rem; }
  .bv-popover--left-end .bv-popover__card {
    margin-bottom: -0.25rem; }
  .bv-popover--right-start .bv-popover__card {
    margin-top: -0.25rem; }
  .bv-popover--right-end .bv-popover__card {
    margin-bottom: -0.25rem; }
  .bv-popover--bottom-start .bv-popover__card {
    margin-left: -0.25rem; }
  .bv-popover--bottom-end .bv-popover__card {
    margin-right: -0.25rem; }
  .bv-popover--is-showing {
    visibility: visible;
    opacity: 1; }
    .bv-popover--is-showing[class*=--top] {
      -webkit-transform: translateY(0);
              transform: translateY(0); }
    .bv-popover--is-showing[class*=--left] {
      -webkit-transform: translateX(0);
              transform: translateX(0); }
    .bv-popover--is-showing[class*=--right] {
      -webkit-transform: translateX(0);
              transform: translateX(0); }
    .bv-popover--is-showing[class*=--bottom] {
      -webkit-transform: translateY(0);
              transform: translateY(0); }
  .bv-popover--small .bv-popover__placement {
    max-width: 12.5rem; }
  .bv-popover--medium .bv-popover__placement {
    max-width: 20rem; }
  .bv-popover--large .bv-popover__placement {
    max-width: 30rem; }

.bv-body--popover-is-showing {
  overflow: hidden; }
