.bv-flex {
  display: flex; }
  .bv-flex__item {
    flex: 1;
    margin: 0 0.5rem; }
    .bv-flex__item:first-child {
      margin-left: 0; }
    .bv-flex__item:last-child {
      margin-right: 0; }
    .bv-flex__item--fixed {
      flex: none; }
    .bv-flex__item--align-top {
      align-self: flex-start; }
    .bv-flex__item--align-center {
      align-self: center; }
    .bv-flex__item--align-bottom {
      align-self: flex-end; }
  .bv-flex__spacer {
    flex: none;
    margin-left: auto;
    margin-right: auto; }
  .bv-flex--is-inline {
    display: inline-flex; }
  .bv-flex--align-top {
    align-items: flex-start; }
  .bv-flex--align-center {
    align-items: center; }
  .bv-flex--align-bottom {
    align-items: flex-end; }
  .bv-flex--compact > .bv-flex__item {
    margin: 0 0.25rem; }
    .bv-flex--compact > .bv-flex__item:first-child {
      margin-left: 0; }
    .bv-flex--compact > .bv-flex__item:last-child {
      margin-right: 0; }
  .bv-flex--comfortable > .bv-flex__item {
    margin: 0 1rem; }
    .bv-flex--comfortable > .bv-flex__item:first-child {
      margin-left: 0; }
    .bv-flex--comfortable > .bv-flex__item:last-child {
      margin-right: 0; }
  .bv-flex--justify-start {
    justify-content: flex-start; }
  .bv-flex--justify-end {
    justify-content: flex-end; }
  .bv-flex--justify-center {
    justify-content: center; }
  .bv-flex--justify-between {
    justify-content: space-between; }
  .bv-flex--justify-around {
    justify-content: space-around; }
  .bv-flex--justify-evenly {
    justify-content: space-evenly; }
