.bv-grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 1rem; 
  margin-top: 5%;}
  .bv-grid__column {
    grid-column: span 12; }
    @media only screen and (min-width: 600px) {
      .bv-grid__column--half {
        grid-column: span 6; }
      .bv-grid__column--third {
        grid-column: span 4; }
      .bv-grid__column--two-thirds {
        grid-column: span 8; }
      .bv-grid__column--quarter {
        grid-column: span 3; }
      .bv-grid__column--three-quarters {
        grid-column: span 9; }
      .bv-grid__column--align-start {
        align-self: start; }
      .bv-grid__column--align-center {
        align-self: center; }
      .bv-grid__column--align-end {
        align-self: end; }
      .bv-grid__column--align-stretch {
        align-self: stretch; }
      .bv-grid__column--justify-start {
        justify-self: start; }
      .bv-grid__column--justify-center {
        justify-self: center; }
      .bv-grid__column--justify-end {
        justify-self: end; }
      .bv-grid__column--justify-stretch {
        justify-self: stretch; } }
  .bv-grid--collapsed {
    grid-gap: 0; }
  .bv-grid--compact {
    grid-gap: 0.5rem; }
  .bv-grid--comfortable {
    grid-gap: 2rem; }
  .bv-grid--align-start {
    align-items: start; }
  .bv-grid--align-center {
    align-items: center; }
  .bv-grid--align-end {
    align-items: end; }
  .bv-grid--align-stretch {
    align-items: stretch; }
  .bv-grid--justify-start {
    justify-items: start; }
  .bv-grid--justify-center {
    justify-items: center; }
  .bv-grid--justify-end {
    justify-items: end; }
  .bv-grid--justify-stretch {
    justify-items: stretch; }
