.bv-icon-button {
  border: none;
  background-color: transparent;
  border-radius: 0.5rem;
  color: inherit;
  cursor: pointer;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0;
  width: 1.25rem;
  height: 1.25rem;
  padding: 0.125rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  transition: opacity 0.15s ease; }
  .bv-icon-button:hover, .bv-icon-button:focus {
