.bv-nav__items {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column; }

.bv-nav__item, .bv-nav__divider, .bv-nav__spacer {
  position: relative; }

.bv-nav__item-link {
  display: flex;
  padding: 0.375rem 0.5rem;
  flex-direction: row;
  flex-wrap: nowrap;
  color: inherit;
  align-items: flex-start;
  transition: 0.3s ease;
  border-radius: 0.5rem;
  text-decoration: none; }

.bv-nav__item-details {
  display: block;
  flex: 1 1 0%;
  padding-top: 0.125rem;
  padding-bottom: 0.125rem;
  overflow: hidden; }

.bv-nav__item-label, .bv-nav__item-description {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.bv-nav__item-description {
  font-size: 0.75rem;
  color: rgba(0, 0, 0, 0.65);
  line-height: 1rem; }

.bv-nav__item-icon-before, .bv-nav__item-icon-after {
  display: inline-block;
  min-width: 1.5rem;
  min-height: 1.5rem; }

/*.bv-nav__item-icon-before {
  padding: 0.25rem; }*/

.bv-nav__item-icon-after {
  padding: 0.25rem;
  align-self: center; }

.bv-nav__item-prefix {
  margin-right: 0.5rem; }

.bv-nav__item-suffix {
  margin-left: 0.5rem;
  align-self: center; }

.bv-nav__item--is-clickable .bv-nav__item-link {
  cursor: pointer; }
  .bv-nav__item--is-clickable .bv-nav__item-link:hover, .bv-nav__item--is-clickable .bv-nav__item-link:focus {
    text-decoration: none;
    color: inherit;
    background-color: rgba(0, 0, 0, 0.03); }
  .bv-nav__item--is-clickable .bv-nav__item-link:active {
    outline: none;
    background-color: rgba(0, 0, 0, 0.1); }

.bv-nav__divider {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  height: 0;
  margin: 0.5rem; }

.bv-nav__spacer {
  min-height: 1rem;
  margin: auto 0; }

.bv-nav--tabs .bv-nav__item {
  padding: 0.125rem 0; }
  .bv-nav--tabs .bv-nav__item-link {
    border-radius: 0;
    padding-left: 1rem;
    padding-right: 1rem; }
    .bv-nav--tabs .bv-nav__item-link:hover, .bv-nav--tabs .bv-nav__item-link:focus {
      box-shadow: 4px 0px 0px 0px rgba(0, 0, 0, 0.1) inset; }
  .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link, .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link:hover, .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link:focus {
    box-shadow: 4px 0px 0px 0px #0A75C2 inset; }

@media only screen and (min-width: 900px) {
  .bv-nav--tabs .bv-nav__items {
    flex-direction: row; }
  .bv-nav--tabs .bv-nav__item {
    padding: 0 0.125rem; }
    .bv-nav--tabs .bv-nav__item-link:hover, .bv-nav--tabs .bv-nav__item-link:focus {
      box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.1) inset; }
    .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link, .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link:hover, .bv-nav--tabs .bv-nav__item--is-active .bv-nav__item-link:focus {
      box-shadow: 0px -4px 0px 0px #0A75C2 inset; }
  .bv-nav--tabs .bv-nav__divider {
    border-top: none;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    margin: 0 0.5rem;
    height: auto;
    width: 0; }
  .bv-nav--tabs .bv-nav__spacer {
    min-height: 0;
    min-width: 1rem;
    margin: 0 auto; } }

@media only screen and (min-width: 900px) {
  .bv-nav--centered .bv-nav__items {
    justify-content: center; } }
