.bv-pill {
  display: inline-flex;
  vertical-align: middle;
  align-items: center;
  margin: 0px 0.3em;
  border: 1px solid #5C6670;
  background-color: #ECEEF0;
  color: #394046;
  font-size: 0.75rem;
  font-weight: 500;
  padding: 0.0625rem 0.375rem;
  line-height: 1rem;
  border-radius: 0.625rem; }
  .bv-pill__label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block; }
  .bv-pill__icon {
    margin-left: -0.125rem;
    margin-right: 0.125rem; }
  .bv-pill__dismiss-button {
    margin-left: 0.125rem;
    margin-right: -0.125rem;
    width: 1rem;
    height: 1rem; }
  .bv-pill--with-info {
    border-color: #3B63FF;
    background-color: #E8ECFF;
    color: #264BD9; }
  .bv-pill--with-warning {
    border-color: #E16D00;
    background-color: #FEF4DC;
    color: #B25600; }
  .bv-pill--with-success {
    border-color: #008757;
    background-color: #DEF6EE;
    color: #00633F; }
  .bv-pill--with-alert {
    border-color: #E41244;
    background-color: #FFEBF0;
    color: #B4002B; }
  .bv-pill--is-minimized {
    padding: 0;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 0.5rem; }
    .bv-pill--is-minimized > .bv-pill__icon, .bv-pill--is-minimized > .bv-pill__label, .bv-pill--is-minimized > .bv-pill__dismiss-button {
      display: none; }
  .bv-pill--is-filled {
    background-color: #5C6670;
    border-color: rgba(0, 0, 0, 0.1);
    color: #fff; }
    .bv-pill--is-filled.bv-pill--with-info {
      background-color: #3B63FF;
      color: #fff; }
    .bv-pill--is-filled.bv-pill--with-warning {
      background-color: #B25600;
      color: #fff; }
    .bv-pill--is-filled.bv-pill--with-success {
      background-color: #008757;
      color: #fff; }
    .bv-pill--is-filled.bv-pill--with-alert {
      background-color: #E41244;
      color: #fff; }
  .bv-pill--is-clickable {
    cursor: pointer; }
