.bv-field {
  position: relative;
  display: block;
  position: relative;
  margin-bottom: 1rem; }
  .bv-field:after {
    content: "";
    clear: both;
    display: block; }
  .bv-field > :last-child {
    margin-bottom: 0; }
  .bv-field:last-child {
    margin-bottom: 0; }
  .bv-field__label {
    margin-bottom: 0.25rem; }
  .bv-field__accessory {
    margin-bottom: 0.25rem; }
  .bv-field__header {
    display: flex;
    justify-content: space-between; }
  .bv-field__help-text {
    font-size: 0.75rem;
    line-height: 1rem;
    color: rgba(0, 0, 0, 0.65); }
  .bv-field__message-icon {
    margin-right: 0.25rem; }
  .bv-field--alert .bv-field__message {
    color: #E41244; }
  .bv-field--warning .bv-field__message {
    color: #B25600; }
  .bv-field--success .bv-field__message {
    color: #008757; }
  .bv-field--is-disabled .bv-field__label {
    color: rgba(0, 0, 0, 0.4); }
