.bv-breadcrumb {
  font-size: 0.8125rem;
  margin-bottom: 0.5rem; }
  .bv-breadcrumb__items {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex; }
  .bv-breadcrumb__item {
    color: rgba(0, 0, 0, 0.2);
    display: inline-flex;
    align-items: center; }
    .bv-breadcrumb__item::after {
      content: "/";
      display: inline-block;
      margin: 0 0.25rem; }
    .bv-breadcrumb__item:last-child::after {
      display: none; }
  .bv-breadcrumb__link, .bv-breadcrumb__stub {
    display: block;
    padding: 0.125rem 0.25rem;
    border-radius: 4px; }
  .bv-breadcrumb__stub {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.8); }
  .bv-breadcrumb__link {
    color: rgba(0, 0, 0, 0.55);
    transition: 0.15s ease; }
    .bv-breadcrumb__link:hover, .bv-breadcrumb__link:active {
      background-color: rgba(0, 0, 0, 0.03);
      text-decoration: none;
      color: rgba(0, 0, 0, 0.65); }
    .bv-breadcrumb__link:active {
      background-color: rgba(0, 0, 0, 0.05); }
