.bv-tooltip {
  position: absolute;
  z-index: 500;
  pointer-events: none; }
  .bv-tooltip__bubble {
    pointer-events: auto;
    position: absolute;
    display: block;
    max-width: 300px;
    min-width: 2rem;
    padding: 0.5rem 0.75rem;
    background-color: rgba(41, 46, 51, 0.95);
    border-radius: 0.5rem;
    color: #fff;
    font-size: 0.8125rem;
    white-space: nowrap;
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s ease; }
  .bv-tooltip__pointer {
    width: 10px;
    height: 10px;
    display: block;
    position: absolute;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border: 5px solid rgba(41, 46, 51, 0.95); }
  .bv-tooltip--is-multiline > .bv-tooltip__bubble {
    width: 300px;
    white-space: normal; }
  .bv-tooltip--is-showing .bv-tooltip__bubble {
    visibility: visible;
    opacity: 1; }
  .bv-tooltip--top .bv-tooltip__bubble {
    bottom: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-bottom: 12px; }
  .bv-tooltip--top .bv-tooltip__pointer {
    bottom: -5px;
    left: 50%;
    margin-left: -5px;
    border-top-color: transparent;
    border-left-color: transparent; }
  .bv-tooltip--left .bv-tooltip__bubble {
    top: 50%;
    right: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-right: 12px; }
  .bv-tooltip--left .bv-tooltip__pointer {
    right: -5px;
    top: 50%;
    margin-top: -5px;
    border-bottom-color: transparent;
    border-left-color: transparent; }
  .bv-tooltip--right .bv-tooltip__bubble {
    top: 50%;
    left: 100%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-left: 12px;
    -webkit-animation-name: from-left;
            animation-name: from-left; }
  .bv-tooltip--right .bv-tooltip__pointer {
    left: -5px;
    top: 50%;
    margin-top: -5px;
    border-top-color: transparent;
    border-right-color: transparent; }
  .bv-tooltip--bottom .bv-tooltip__bubble {
    top: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 12px; }
  .bv-tooltip--bottom .bv-tooltip__pointer {
    top: -5px;
    left: 50%;
    margin-left: -5px;
    border-bottom-color: transparent;
    border-right-color: transparent; }
  .bv-tooltip-trigger-element.bv-icon {
