.bv-container {
  position: relative;
  display: block;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto; }
  .bv-container:after {
    content: "";
    clear: both;
    display: block; }
  .bv-container > :last-child {
    margin-bottom: 0; }
  .bv-container--small {
    max-width: 400px; }
  .bv-container--medium {
    max-width: 800px; }
  .bv-container--full {
    max-width: 100%; }
