.bv-card {
  position: relative;
  display: block;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  overflow: hidden; }
  .bv-card:after {
    content: "";
    clear: both;
    display: block; }
  .bv-card > :last-child {
    margin-bottom: 0; }
  .bv-card__content {
    position: relative;
    display: block;
    padding: 1.5rem; }
    .bv-card__content:after {
      content: "";
      clear: both;
      display: block; }
    .bv-card__content > :last-child {
      margin-bottom: 0; }
  .bv-card__section {
    margin-bottom: 1rem; }
  .bv-card__header {
    display: flex; }
    .bv-card__header-info {
      flex: 1; }
    .bv-card__header-actions {
      flex: none;
      margin-left: 0.5rem; }
    .bv-card__header-title {
      font-size: 1.125rem;
      font-weight: 600;
      margin: 0 auto 0 0; }
    .bv-card__header-intro {
      font-size: 1rem;
      color: rgba(0, 0, 0, 0.65); }
      .bv-card__header-intro:last-child {
        margin: 0; }
    .bv-card__header .bv-link {
      margin-left: 0.5rem;
      margin-right: 0.5rem; }
    .bv-card__header--banner {
      margin: -1.5rem -1.5rem 1.5rem -1.5rem;
      padding: 0.5rem;
      color: #fff;
      background: #1e94f6;
      justify-content: center; }
  .bv-card__footer {
    display: flex;
    align-items: center; }
    .bv-card__footer .bv-flex-spacer {
      margin: 0 auto; }
  .bv-card__dismiss-button {
    z-index: 1;
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
    width: 1.25rem;
    height: 1.25rem; }
    .bv-card__dismiss-button .bv-icon {
      width: 0.75rem;
      height: 0.75rem;
      min-width: 0.75rem;
      min-height: 0.75rem; }
  .bv-card--is-sectioned .bv-card__header, .bv-card--is-sectioned .bv-card__section, .bv-card--is-sectioned .bv-card__footer {
    flex: 1;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    margin: 0 -1.5rem; }
    .bv-card--is-sectioned .bv-card__header:first-child, .bv-card--is-sectioned .bv-card__section:first-child, .bv-card--is-sectioned .bv-card__footer:first-child {
      border-top-left-radius: 0.5rem;
      border-top-right-radius: 0.5rem;
      margin-top: -1.5rem; }
    .bv-card--is-sectioned .bv-card__header:last-child, .bv-card--is-sectioned .bv-card__section:last-child, .bv-card--is-sectioned .bv-card__footer:last-child {
      border-bottom: none;
      border-bottom-left-radius: 0.5rem;
      border-bottom-right-radius: 0.5rem;
      margin-bottom: -1.5rem; }
  .bv-card--is-sectioned .bv-card__header {
    margin-top: -1.5rem; }
  .bv-card--is-sectioned .bv-card__footer {
    margin-bottom: -1.5rem;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.03); }
  .bv-card--is-centered {
    margin-left: auto;
    margin-right: auto; }
  .bv-card--is-inset {
    background-color: rgba(0, 0, 0, 0.05);
    box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.1); }
  .bv-card--is-selected {
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #0A75C2; }
  .bv-card--small {
    max-width: 300px; }
  .bv-card--medium {
    max-width: 600px; }
  .bv-card--large {
    max-width: 900px; }
